import React, { useEffect, useContext, useState, useRef } from "react";
import Container from "react-bootstrap/Container";
import Form from "react-bootstrap/Form";
import Button from "react-bootstrap/Button";
import Col from "react-bootstrap/Col";
import Row from "react-bootstrap/Row";
import { Typeahead } from "react-bootstrap-typeahead";
import { WrappedParse as Parse } from "../vendor/ParseApp";
import { getPdfText } from "../helpers/pdf";
import { countries } from "../helpers/country";
import nationality from "../helpers/nationality";
import { employmentType, occupation } from "../helpers/occupation";
import AppContext from "../context/AppContext";
import { useTranslation } from "gatsby-plugin-react-i18next";

export default function App({ newApplication = false }) {
  const context = useContext(AppContext);
  const { t } = useTranslation();
  const [file, setFile] = React.useState(null);
  const [message, setMessage] = React.useState("");
  const [model, setModel] = React.useState({
    tags: [],
    location: [],
  });

  const fileInput = useRef(null);

  const clickProfile = () => {
    fileInput?.current.click();
  };

  const setModelValue = (name, value) => {
    setModel({
      ...model,
      [name]: value,
    });
  };

  const run = async () => {
    context.showLoading(true);
    await Parse.Cloud.run("getMyApplication").then((app) => {
      if (app && app.get) {
        setModel(app?.get("data"));
      }
    });
    context.showLoading(false);
  };
  useEffect(() => {
    if (!newApplication) {
      run();
    }
  }, [context.showLoading]);

  return (
    <>
      <Container className="">
        <h3>{t("myapplication")}</h3>
        <Row>
          <Col>
            <img
              onClick={() => clickProfile()}
              src={model.photoUrl || "/assets/images/profile.jpg"}
              alt="mdo"
              width="256"
              height="256"
              class="rounded-circle me-1"
            ></img>
            <input
              name="document"
              ref={fileInput}
              className="file-upload d-none"
              type="file"
              accept="image/*"
              onChange={async (event) => {
                const parseFile = new Parse.File(
                  "document",
                  event.target.files[0]
                );
                const document = await parseFile.save();
                const url = document.url();

                const newModel = {
                  ...model,
                  photoUrl: url,
                };
                setModel(newModel);
              }}
            />
          </Col>
        </Row>

        <Row>
          <Col>
            <Form.Group className="mb-3">
              <Form.Label> {t("firstName")}</Form.Label>
              <Form.Control
                type="text"
                placeholder=""
                value={model.name}
                onChange={(v) => setModelValue("name", v.target.value)}
              />
            </Form.Group>
          </Col>
          <Col>
            <Form.Group className="mb-3">
              <Form.Label> {t("familyName")}</Form.Label>
              <Form.Control
                value={model.familyName}
                type="text"
                placeholder=""
                onChange={(v) => setModelValue("familyName", v.target.value)}
              />
            </Form.Group>
          </Col>
        </Row>

        <Row>
          {newApplication && (
            <Col>
              <Form.Group className="mb-3">
                <Form.Label> {t("email")}</Form.Label>
                <Form.Control
                  type="text"
                  placeholder=""
                  value={model.email}
                  onChange={(v) => setModelValue("email", v.target.value)}
                />
              </Form.Group>
            </Col>
          )}
          <Col>
            <Form.Group className="mb-3">
              <Form.Label> {t("contact")}</Form.Label>
              <Form.Control
                type="text"
                placeholder=""
                value={model.contact}
                onChange={(v) => setModelValue("contact", v.target.value)}
              />
            </Form.Group>
          </Col>
        </Row>

        <Row></Row>
        <Row>
          <Col>
            <Form.Group>
              <Form.Label> {t("seekJob")}</Form.Label>
              <Typeahead
                allowNew={true}
                id="basic-typeahead-multiple"
                multiple
                onChange={(d) => setModelValue("tags", d)}
                options={occupation}
                placeholder="Type in or select from  the list of jobs you are looking for"
                selected={model.tags}
              />
            </Form.Group>
          </Col>
        </Row>

        <Row>
          <Col>
            <Form.Group
              className="mb-3"
              controlId="exampleForm.ControlTextarea1"
            >
              <Form.Group>
                <Form.Label> {t("workLocation")}</Form.Label>
                <Typeahead
                  allowNew={true}
                  id="basic-typeahead-multiple"
                  labelKey="label"
                  multiple
                  selected={model.location}
                  onChange={(d) => setModelValue("location", d)}
                  options={countries}
                  placeholder="Type in or select from  the list"
                />
              </Form.Group>
            </Form.Group>
          </Col>
          <Col>
            <Form.Group className="mb-3">
              <Form.Label> {t("citizenship")}</Form.Label>

              <Typeahead
                allowNew={false}
                id="basic-typeahead-multiple"
                onChange={(d) => setModelValue("citizenship", d)}
                options={nationality}
                placeholder="Type in or select from  the list"
                selected={model.citizenship}
              />
            </Form.Group>
          </Col>
        </Row>

        <Row>
          <Col>
            <Form.Group
              className="mb-3"
              controlId="exampleForm.ControlTextarea1"
            >
              <Form.Label>CV</Form.Label>
              <Form.Control
                name="document"
                type="file"
                rows={3}
                onChange={async (e) => {
                  setFile(e.target.files[0]);
                }}
              />

              {model.docUrl ? (
                <a
                  className="color-brand-1"
                  href={model.docUrl}
                  target="_blank"
                >
                  {" "}
                  {t("downloadFile")}{" "}
                </a>
              ) : (
                ""
              )}
            </Form.Group>
          </Col>

          <Col>
            <Form.Group
              className="mb-3"
              controlId="exampleForm.ControlTextarea1"
            >
              <Form.Group>
                <Form.Label>{t("employmentType")}</Form.Label>
                <Typeahead
                  allowNew={true}
                  id="basic-typeahead-multiple"
                  labelKey="label"
                  multiple
                  selected={model.employmentType}
                  onChange={(d) => setModelValue("employmentType", d)}
                  options={employmentType}
                  placeholder="Type in or select from  the list"
                />
              </Form.Group>
            </Form.Group>
          </Col>
        </Row>

        <Row>
          <Col>
            <Form.Group className="mb-3">
              <Form.Label>{t("expectSalary")}</Form.Label>
              <Form.Control
                type="text"
                placeholder=""
                value={model.expectSalary}
                onChange={(e) => setModelValue("expectSalary", e.target.value)}
              />
            </Form.Group>
          </Col>

          <Col>
            <Form.Group className="mb-3">
              <Form.Label>{t("noticePeriod")}</Form.Label>
              <Form.Control
                type="text"
                placeholder=""
                value={model.noticePeriod}
                onChange={(e) => setModelValue("noticePeriod", e.target.value)}
              />
            </Form.Group>
          </Col>
        </Row>
        <Row>
          <Col>
            {!newApplication && (
              <Form>
                <Form.Check
                  type="switch"
                  id="custom-switch"
                  label={t("openToWork")}
                  checked={model.consent}
                  onChange={() => {
                    setModelValue("consent", !model.consent);
                  }}
                />
              </Form>
            )}
            <Button
              variant="primary"
              onClick={async () => {
                context.showLoading(true);
                const method = newApplication
                  ? "createNewApplication"
                  : "updateApplication";
                if (file) {
                  const parseFile = new Parse.File("document", file);
                  const document = await parseFile.save();
                  const data = await document.getData();
                  const output = await getPdfText(atob(data));
                  const url = document.url();

                  const newModel = {
                    ...model,
                    docUrl: url,
                  };
                  setModel(newModel);

                  try {
                    const result = await Parse.Cloud.run(method, {
                      search_field: output,
                      model: {
                        data: newModel,
                      },
                    });
                    setMessage(t("profileSaved"));
                  } catch (e) {
                    setMessage(e.message);
                  }
                } else {
                  try {
                    const result = await Parse.Cloud.run(method, {
                      model: {
                        data: model,
                      },
                    });
                    setMessage(t("profileSaved"));
                  } catch (e) {
                    setMessage(e.message);
                  }
                }
                context.showLoading(false);
              }}
            >
              Update
            </Button>
            <div>{message}</div>
          </Col>
        </Row>
      </Container>
    </>
  );
}

export const Head: HeadFC = () => (
  <>
    <title></title>
    <script src="/assets/pdf/pdf.js" type="text/javascript" />
  </>
);
